const util = require('../../util/util.js');
import navTab from '../../components/navTab.vue';
import tabBar4 from '../../components/tabBar4.vue';
import musicPlayer from './info/index.vue';
export default {
	components: { navTab, tabBar4, musicPlayer },
	data() {
		return {
			isClickDownLoad: null,
			isClickPlay: null,
			showHot: null,
			name: '',
			authName: '',
			picUrl: '',
			playState: 0,
			showMusicDetail: null,
			toView: '', //回到顶部id
			currentPage: 'musicPage',
			currentTab: 0, //sweiper所在页
			pages: [1, 1, 1, 1, 1, 1, 1, 1, 1], //第几页存储
			paraForEachPage: [],
			input: '',
			tabTitle: ['推荐', '', '', '', ''], //导航栏格式
			list: [], //数据格式
			hotCateGory: [],
			hotList: []
		};
	},
	onLoad(e) {
		this.urlRequest();

		this.getHot();
	},
	onShow() {},
	onHide() {},
	methods: {
		toTop() {
			this.toView = '';
			setTimeout(() => {
				this.toView = 'top' + this.currentTab;
			}, 10);
		},

		//显示热门播放列表
		showHotButton(e, value) {
			//这是关闭窗口
			if (value == null) {
				this.showHot = e;
				this.pages[0] = '1';
				this.paraForEachPage[0].id = '';

				//延迟渲染,让页面先弹出来
				let _open = setTimeout(() => {
					this.hotList = [];
					_open = null;
				}, 500);
				return;
			}

			var id = value.id;
			this.paraForEachPage[0].id = id;
			this.getMoreHost();
		},
		async getMoreHost() {
			var pagePara = this.paraForEachPage[0];
			var input = this.input;
			var id = pagePara.id;
			var page = this.pages[0];
			var pageSize = '20';

			var par = {
				id: id,
				input: input,
				page: page,
				pageSize: pageSize
			};

			const [err, ret] = await uni.request({
				url: this.$serverUrl + '/getMusicListByCategory',
				data: par,
				method: 'POST',
				header: {
					'content-type': 'application/json'
				}
			});

			if (err) {
				console.log(err);
			} else {
				//延迟渲染,让页面先弹出来

				this.pages[this.currentTab]++;
				let data = ret.data;
				var templist = this.hotList.concat(data);
				this.hotList = templist;
				this.$forceUpdate();
				this.showHot = true;
			}
		},
		//显示歌曲详情页面
		showMusicDetailFuc() {
			this.showMusicDetail = true;
		},
		//子类组件调用父类
		fatherMethod() {
			this.showMusicDetail = null;
		},
		//父类调用子类
		clickParent() {
			this.playState = this.$refs.mychild.play();
		},

		//子类状态改变了
		changeState(e) {
			this.playState = e;
		},

		changeSongMsg(e) {
			console.log('接收到子类的歌曲信息了' + e.name + e.authorName);
			this.name = e.name;
			this.authName = e.authorName;
			this.picUrl = e.picUrl;
		},
		changeTab(index) {
			this.currentTab = index;
		},

		// swiper 滑动
		swiperTab: function(e) {
			var index = e.detail.current; //获取索引
			this.$refs.navTab.longClick(index);

			if (this.input.length == 0) {
				return;
			}

			//第一页时top500,不要去那搜索
			if (this.currentTab == 0) {
				return;
			}

			//滑动的时候判断有么有值,没有的话取获取一下
			let currentList = this.list[this.currentTab];
			if (currentList.length == 0) {
				var pagePara = this.paraForEachPage[this.currentTab];
				var id = pagePara.id;
				var page = this.pages[this.currentTab];
				var pageSize = '20';

				var par = {
					id: id,
					input: this.input,
					page: page,
					pageSize: pageSize
				};
				console.log('para is ' + pagePara);

				this.urlRequestSerch(par);
			}
		},
		// 加载更多 util.throttle为防抖函数
		lower1: util.throttle(function(e) {
			if (this.currentTab == 0) {
				return;
			}

			console.log(`加载${this.currentTab}`); //currentTab表示当前所在页数 根据当前所在页数发起请求并带上page页数
			uni.showLoading({
				title: '加载中',
				mask: true
			});

			var pagePara = this.paraForEachPage[this.currentTab];
			var input = this.input;
			var id = pagePara.id;
			var page = this.pages[this.currentTab];
			var pageSize = '20';

			var par = {
				id: id,
				input: input,
				page: page,
				pageSize: pageSize
			};
			this.urlRequestNextPage(par);
		}, 300),

		goUrl: function(str, val) {
			this.isClickPlay = str;
			this.$refs.mychild.fatherToPlay(val);
		},
		//获取音乐资源的标题
		async urlRequest() {
			const [err, ret] = await uni.request({
				url: this.$serverUrl + '/getMusicInit',
				//data: [],
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				timeout: '100000'
			});

			if (err) {
				console.log(err);
			} else {
				let data = ret.data;
				this.paraForEachPage = data;

				//初始化标题
				this.tabTitle = [];

				//初始化每页的第几页
				this.pages = [];

				//初始化list详情
				this.list = [];

				for (var i = 0; i < data.length; i++) {
					var e = data[i];
					this.tabTitle.push(e.showName);

					this.pages.push(e.page);

					this.list.push([]);
				}
				console.log(data);

				//初始化top500
				var par = {
					id: '0',
					input: '',
					page: '1',
					pageSize: '20'
				};
				console.log('para is ' + par);

				this.urlRequestSerch(par);
			}
		},

		//获取更多音乐下一页
		async urlRequestNextPage(par) {
			const [err, ret] = await uni.request({
				url: this.$serverUrl + '/musicSearch',
				data: par,
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				timeout: '100000'
			});

			uni.hideLoading();
			if (err) {
				console.log(err);
			} else {
				let data = ret.data;

				if (data.length == 0) {
					uni.showToast({
						icon: 'success',
						title: '没有更多了'
					});
					return;
				}

				this.pages[this.currentTab]++;
				var templist = this.list[this.currentTab].concat(data);
				this.list[this.currentTab] = templist;
				this.$forceUpdate();
				console.log(data);
			}
		},
		//搜索
		confirm(e) {
			console.log('输入了' + e.detail.value);

			//收起键盘

			uni.hideKeyboard();





			if (e.detail.value.trim().length == 0 || this.currentTab == 0) {
				return;
			}

			var pagePara = this.paraForEachPage[this.currentTab];
			var input = e.detail.value;
			if (this.input == input) {
				return;
			}

			this.input = input;

			var id = pagePara.id;

			//如果时输入的话,page应该一直都是1之前内容都清空 排行榜的不要动

			for (var i = 1; i < this.pages.length; i++) {
				this.pages[i] = 1;
				this.list[i] = [];
			}

			this.pages[this.currentTab] == 1;
			var page = this.pages[this.currentTab];

			var pageSize = '20';

			var par = {
				id: id,
				input: input,
				page: page,
				pageSize: pageSize
			};
			console.log('para is ' + pagePara);

			this.urlRequestSerch(par);
		},
		//获取音乐资源
		async urlRequestSerch(data) {
			uni.showLoading({
				title: '加载中',
				mask: true
			});
			const [err, ret] = await uni.request({
				url: this.$serverUrl + '/musicSearch',
				data: data,
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				timeout: '100000'
			});

			uni.hideLoading();
			if (err) {
				console.log(err);
			} else {
				let data = ret.data;
				this.list[this.currentTab] = data;

				this.$forceUpdate();
				console.log(data);
			}
		},
		playAll() {
			var allPlay = this.list[this.currentTab];

			if (allPlay.length == 0) {
				uni.showToast({
					icon: 'none',
					title: `播放列表不能为空`
				});
				return;
			}

			//少循环一个,当作参数传过去
			let musicList = uni.getStorageSync('musicList') || [];
			for (var i = allPlay.length - 1; i > 0; i--) {
				var mus = allPlay[i];

				var musicIds = mus.songUrl.split('/');
				var musicId = musicIds[musicIds.length - 1];
				var musicCach = [musicId, mus];

				for (var j = 0; j < musicList.length; j++) {
					var musCache = musicList[j];

					if (musicCach[0] == musCache[0]) {
						//删除放在前面
						musicList.splice(j, 1);
						j = j - 1;

						break;
					}
				}

				musicList.unshift(musicCach);
			}

			uni.setStorageSync('musicList', musicList);
			this.goUrl('info/index', allPlay[0]);
		},
		async getHot() {
			const [err, ret] = await uni.request({
				url: this.$serverUrl + '/getMusicCateGory',
				data: '',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				}
			});

			if (err) {
				console.log(err);
			} else {
				let data = ret.data;
				this.hotCateGory = data;

				this.$forceUpdate();
				console.log(data);
			}
		},
		downLoad(e, index) {
			this.isClickDownLoad = index;
			console.log('进来下载页面了');
			/* uni.showToast({
				icon: 'success',
				title: `正在下载 `+e.name
			}); */
		}
	}
};
